  justify-content: flex-end; }

/***************Titulos***************/
.titulo {
  font-size: 3.8rem;
  text-align: center;
  font-family: "Krub", sans-serif; }

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
  width: 90%; }

.boton-azul {
  background-color: #231a70;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: "block";
  border: none; }
  .boton-azul:hover {
    background-color: #1d155b;
    cursor: pointer; }

.boton-amarillo {
  background-color: #ffc107;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none; }
  .boton-amarillo:hover {
    background-color: #edb100;
    cursor: pointer; }

.boton-amarillo-block {
  background-color: #ffc107;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none; }
  .boton-amarillo-block:hover {
    background-color: #edb100;
    cursor: pointer; }

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none; }
  .boton-verde:hover {
    background-color: #619800;
    cursor: pointer; }

.dark-mode {
  background-color: #1a1a1a; }
  .dark-mode p {
    color: #FFFFFF; }
  .dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
    color: #757575; }
  .dark-mode .anuncio,
  .dark-mode .resumen-propiedad {
    background-color: #333333;
    border: 1px solid #333333; }
    .dark-mode .anuncio .precio,
    .dark-mode .resumen-propiedad .precio {
      color: #71B100; }
    .dark-mode .anuncio .icono,
    .dark-mode .resumen-propiedad .icono {
      filter: invert(100%); }
  .dark-mode .texto-nosotros blockquote {
    color: #FFFFFF; }
  .dark-mode .formulario label,
  .dark-mode .formulario legend,
  .dark-mode .formulario p {
    color: #757575; }
  .dark-mode .formulario input:not([type="submit"]),
  .dark-mode .formulario textarea,
  .dark-mode .formulario select {
    background-color: #333333;
    border: 1px solid #333333;
    color: #FFFFFF; }

/*UI**/
/*Navegación Principal*/
header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #333333; }
  header h2 {
    color: #FFFFFF;
    font-weight: 700;
    font-size: 3rem;
    width: min-content; }
    header h2 span {
      font-size: 1.5rem; }

.navegacion {
  background-color: #0097a7;
  display: none;
  justify-content: space-around;
  visibility: hidden;
  transition: visibility .3s ease-in-out, opacity .5s linear, display 5s linear; }
  @media (min-width: 768px) {
    .navegacion {
      display: flex;
      visibility: visible;
      opacity: 1; } }

.mostrar {
  display: flex;
  visibility: visible;
  opacity: 1; }

.navegacion-principal {
  margin: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center; }
  @media (min-width: 768px) {
    .navegacion-principal {
      flex-direction: row;
      justify-content: space-between; } }
  .navegacion-principal a {
    color: #FFFFFF;
    font-size: 2rem;
    font-weight: bold;
    width: 100%;
    padding: 2rem; }
    .navegacion-principal a:hover {
      background-color: #ffc107;
      color: #0097a7; }

.no-webp .hero {
  background-image: url(../img/hero1.jpg); }

.webp .hero {
  background-image: url(../img/hero1.webp); }

.hero {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 50rem;
  position: relative; }

.hero::after {
  content: '';
  background-color: rgba(39, 14, 36, 0.5);
  display: block;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute; }

.contenido-hero {
  z-index: 3;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  color: #FFFFFF;
  font-weight: 700; }
  .contenido-hero p {
    color: #FFFFFF; }

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none; } }

.mobile-menu img {
  width: 5rem; }

body {
  font-size: 16px;
  /*1rem=10px*/
  margin: 0;
  /*HHaciendo un degradado*/
  background-image: linear-gradient(to top, #DFE9F3 0%, var(--blanco)); }

.servicios-contenedor {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.servicios {
  display: flex;
  flex-direction: column; }
  @media (min-width: 768px) {
    .servicios {
      flex-direction: row; } }

.servicio {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  align-items: center;
  padding: 1rem; }
  @media (min-width: 768px) {
    .servicio {
      flex: 0 0 calc(33.3% - 1rem);
      align-items: center; } }

.iconos {
  display: flex;
  height: 100px;
  width: 100px;
  justify-content: space-evenly;
  align-items: center;
  background-color: #ffc107;
  border-radius: 50%; }

.iconos svg {
  font-size: 2rem;
  color: #ffc107; }

.copyright {
  background-color: #333333;
  color: #FFFFFF;
  padding: 1rem;
  margin: 0;
  text-align: center; }

.footer {
  background-color: #0097a7;
  display: flex;
  margin-top: 2rem;
  justify-content: space-around; }

.widget nav {
  display: flex;
  flex-direction: column; }

.widget h3 {
  font-size: 1.8rem; }

.paquetes {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center; }
  @media (min-width: 768px) {
    .paquetes {
      flex-direction: row; } }
  .paquetes .paquete {
    width: unset;
    display: flex;
    flex-direction: column;
    flex-basis: calc(100% - 4rem);
    margin: 2rem;
    padding: 2rem;
    text-align: center;
    border: 1px solid #0097a7; }
    @media (min-width: 768px) {
      .paquetes .paquete {
        flex-basis: calc(30% -2rem); } }
    .paquetes .paquete h2 {
      width: min-content; }
    .paquetes .paquete ul {
      list-style: none;
      margin: 1rem 0; }

.equipos-computo {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }
  @media (min-width: 768px) {
    .equipos-computo {
      flex-direction: row; } }
  .equipos-computo .equipo {
    display: flex;
    flex-direction: column;
    flex-basis: calc(100% - 4rem);
    margin: 2rem;
    padding: 2rem;
    text-align: center;
    border: 1px solid #0097a7; }
    @media (min-width: 768px) {
      .equipos-computo .equipo {
        flex-basis: calc(30% - 2rem); } }
    .equipos-computo .equipo h2 {
      width: min-content; }
    .equipos-computo .equipo ul {
      list-style: none;
      margin: 1rem 0; }
    .equipos-computo .equipo img {
      width: 100%;
      height: auto; }

/*Internas*/
.nosotros {
  display: flex;
  justify-content: space-around; }
  .nosotros > * {
    flex-basis: calc(50% - 5rem); }
  .nosotros img {
    border-radius: 15px; }
